@use 'uswds-core' as *;

.usa-prose > {
  h1,
  h2,
  h3,
  h4 {
    + * {
      margin-top: 0.4em;
    }
  }

  h1,
  h2,
  h3 {
    letter-spacing: -0.2px;
  }

  * + h2,
  * + h3 {
    margin-top: 1.7em;
  }

  h4 {
    letter-spacing: -0.1px;
  }

  * + h4 {
    margin-top: 2.5em;
  }

  h5,
  h6 {
    @include u-font-family('sans');
    text-transform: uppercase;

    + * {
      margin-top: 0.2em;
    }
  }

  * + h5,
  * + h6 {
    margin-top: 3em;
  }

  h5 {
    letter-spacing: 0.2px;
  }

  h6 {
    @include u-font-size('sans', 'micro');
    @include u-font-weight('bold');
    letter-spacing: 0.3px;
  }
}
